import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CardTypeComponent } from './card-type/card-type.component';
import { ChooseCardComponent } from './choose-card/choose-card.component';
import { FillCardComponent } from './fill-card/fill-card.component';

const routes: Routes = [

  {path:'cards-type', component:CardTypeComponent},
  { path: 'choose-card/:category', component:ChooseCardComponent },
  { path: 'fill-card/:card', component:FillCardComponent },

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
