import { Component, OnInit, AfterViewInit } from '@angular/core';
import {Router} from "@angular/router";
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-choose-card',
  templateUrl: './choose-card.component.html',
  styleUrls: ['./choose-card.component.css']
})
export class ChooseCardComponent implements OnInit, AfterViewInit {

  category: any;
  
  constructor(
    private route: ActivatedRoute,
    private router: Router
  ){this.route.params.subscribe(params => {this.category = params.category; this.ngOnInit()})}

  ngOnInit() {
  }

  ngAfterViewInit(){

    var cards = document.getElementsByClassName(this.category);
    // if(cards.length==1){
    //   this.router.navigate(['/fill-card/' + 1])
    // }
    var i;
    for (i = 0; i < cards.length; i++) {
      cards[i].setAttribute("style", "display:block");
    }
    
  }



}
