import { Component, OnInit, AfterViewInit } from '@angular/core';
import {Router} from "@angular/router";
import { ActivatedRoute } from '@angular/router';
import { ReactiveFormsModule, FormGroup, FormBuilder, Validators, FormsModule, FormArray } from '@angular/forms';

interface Card{
  no?: string;
  to_x?: number,
  to_y?: number,
  from_x?: number,
  from_y?: number,
}

@Component({
  selector: 'app-fill-card',
  templateUrl: './fill-card.component.html',
  styleUrls: ['./fill-card.component.css']
})
export class FillCardComponent implements OnInit, AfterViewInit{

  card: Card = {};
  form: FormGroup;

  constructor(
    private route: ActivatedRoute,
    public fb: FormBuilder,
  ){
    this.route.params.subscribe(params => {this.card.no = params.card; this.ngOnInit()})

    this.form = fb.group({
      from : ["", Validators.required],
      to : ["", Validators.required],
    });

  }

  ngAfterViewInit(){
    if(this.card.no=='0'){
      this.card.to_x = 555; this.card.to_y = 415;
      this.card.from_x = 555; this.card.from_y = 700;
    }
    
    if(this.card.no=='1'){
      this.card.to_x = 700; this.card.to_y = 415;
      this.card.from_x = 700; this.card.from_y = 700;
    }

    this.update();
  }

  ngOnInit(){
  }

  
  update(){
    var toValue = (<HTMLInputElement>document.getElementById("to")).value;
    var fromValue = (<HTMLInputElement>document.getElementById("from")).value;
    this.myCanvas(toValue, fromValue);
  }


  myCanvas(to:string, from){


    var canvas = <HTMLCanvasElement>document.getElementById('myCanvas');
    var ctx = canvas.getContext("2d");
    var img = new Image();

    img.onload = ()=>{
      ctx.drawImage(img, 0,0, canvas.width, canvas.height);
      ctx.font = "bold 32px 'Droid Arabic Kufi' ";
      ctx.fillText(to, this.card.to_x,this.card.to_y);
      ctx.fillText(from, this.card.from_x,this.card.from_y);
      ctx.font = '32px cairo';
      ctx.direction = 'rtl';
      document.getElementById("download").setAttribute("href", canvas.toDataURL());
    }

    img.src = "assets/cards/" + this.card.no + ".jpg";

  }

}
